@import url('https://fonts.googleapis.com/css2?family=Kalam&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  
}

.navbar {
  font-family: Georgia, 'Times New Roman', Times, serif;
  font-size: 18px;
  line-height: 25px;
  text-transform: capitalize;
  box-shadow: 0px 12px 8px -8px grey; 
  background: #ffefba;
  border-bottom: 0px solid grey !important;
  font-weight: bold;
  
}

.search-form {
  margin-right: 200px;
  margin-top: 4px;
  width: 400px;
  /* padding: 10px; */
}
.search-form input {
  border: 1px blueviolet solid;
  border-radius: 10px;
  
}

.search-form button {
  background-color: blueviolet;
  border-radius: 10px;
  color: white;

}

.nav-link {
  font-weight: 700px !important;
  margin-right: 20px;
  
}

.active {
  border-bottom: 1px solid black;
}

.navbar-brand {
  font-weight: 700;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  color: rgb(69, 5, 129);
  font-size: 24px;
}

.footer {
  color: white;
  padding: 25px;
  background: #000000;
  background: -webkit-linear-gradient(to right, #434343, #000000);
  background: linear-gradient(to right, #434343, #000000);
}

.footer a {
  text-decoration: none;
  color: white;
  padding: 10px;
}

.footer a:hover {
  color: #ffefba;
  border-bottom: solid #ffefba;
}

.page {
  display: flex;
  flex-direction: column;
  min-height: 65vh;
  align-items: center;
  justify-content: center;
}

.pageTitle {
  font-size: 50px;
  font-weight: bolder;
}

.pageButton {
  color: black;
  border: 3px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
  border-radius: 20px;
  font-weight: bold;
}

.register {
  margin-top: 40px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  
  /* background-color: #000000; */
}


.contain{
  background-color:mintcream;
    padding: 5px 20px 15px 20px;
    border: 1px solid rgb(79, 201, 142);
    border-radius: 4px;
    width: 40%;
    
}

.form-control{
  border: 1.5px solid rgb(61, 205, 114);
}

.login{
  width: 30%;
  margin-top: 80px;
  margin-bottom: 30px;
}

.reset{
  width:200px;
  margin: 20px;
 
}

.logbtn{
  width: 350px;
}